$COLOR_PRIMARY: #bc6ff1;
$COLOR_SECONDARY: #892cdc;
$COLOR_TERTIARY: #52057b;
$COLOR_BLACK: #000000;
$COLOR_WHITE: #dddddd;

* {
  padding: 0;
  margin: 0;
}

html,
body {
  // overflow-x: hidden;
  font-family: "Times New Roman", Times, serif;
  font-size: 1.1em;
  background-color: $COLOR_BLACK;
  background-size: cover;
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}
html::-webkit-scrollbar {
  width: 0;
  background: transparent;
}
a {
  color: $COLOR_SECONDARY;
  text-decoration: none;
}

a:hover {
  color: $COLOR_TERTIARY;
}

.name {
  font-weight: 800;
  color: $COLOR_SECONDARY;
}
.life {
  margin-bottom: 35px;
}
.skills {
  margin-bottom: 10px;
}
.typos {
  font-family: "Playfair Display";
  font-weight: 300;
}

.main-title {
  padding: 20px;
  color: $COLOR_SECONDARY;
}

.header-typos {
  font-family: "Playfair Display", sans-serif;
  font-weight: 500;
  font-size: 24px;
  text-align: center;
  color: $COLOR_SECONDARY;
}

// container

.container {
  max-width: 90%;
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0 auto;
  padding: 5px;
  color: $COLOR_WHITE;
  height: 100%;
  &-section {
    margin-top: 5px;
    width: 100%;
  }
  &-skills {
    padding: 10px;
    margin: 10px;
  }
  &-interests {
    padding: 30px;
    margin: 10px;
    border: 2px solid $COLOR_SECONDARY;
    border-radius: 10px;
  }
  &-projects {
    padding: 10px;
    margin: 10px;
  }
  .card-content {
    background-color: $COLOR_SECONDARY;
    color: $COLOR_WHITE;
    text-align: center;
  }
  .typos-description {
    border: 2px solid $COLOR_TERTIARY;
    margin-bottom: 5%;
    margin-top: 5px;
    padding: 10px;
    text-align: center;
    border-radius: 15px;
  }
}

.navbar-container {
  background-color: $COLOR_BLACK;
  height: 70px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.skills-item {
  border-radius: 10px;
  border: 2px solid $COLOR_SECONDARY;
  margin-bottom: 5px;
  &__title {
    color: $COLOR_PRIMARY;
  }
  &__text {
    color: $COLOR_WHITE;
  }
}
.copyright {
  margin-top: 15px;
}

.contact {
  margin-top: 20px;
  border: 2px solid $COLOR_TERTIARY;
  border-radius: 10px;
  padding: 10px;
}
